* {
  padding: 0;
  margin: 0;
  --darkgreen: #164a41;
  --MediumGreen: #4d774e;
  --LightGreen: #9dc88d;
  --NaturalYellow: #f1b24a;
  --White: #ffffff;
}
body {
  background-color: var(--White);
}
.header {
  background-color: var(--darkgreen);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 13px;
  color: white;
}

.header img {
  filter: invert(1);
}

.globalcontainer {
  background-color: var(--NaturalYellow);
  margin: 20px;
  align-self: center;
  display: flex;
  flex-direction: column;
  color: white;
}

.global-items {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  padding: 12px;
  background-color: var(--darkgreen);
}
.global-title {
  display: flex;
  font-size: x-large;
  height: 50px;
}

.global-title p {
  margin: auto;
}

.items {
  background-color: var(--MediumGreen);
  width: 320px;
  flex-wrap: wrap;
  margin: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}

.title-global {
  width: 100%;
  display: flex;
}
.title-global h2 {
  margin: auto;
}

.title-global-value {
  font-size: x-large;
  font-weight: 500;
  padding: 10px;
}

/* select view style */

.global-title {
  display: flex;
  justify-content: center;
}


